
<template>
    <div class="my-container">

        <div id="app" class="app1">
          <div>
          <waterfall
        :align="align"
        :line-gap="200"
        :min-line-gap="100"
        :max-line-gap="220"
        :single-max-width="300"
        :watch="items"
        @reflowed="reflowed"
        ref="waterfall"
      >
        <!-- each component is wrapped by a waterfall slot -->
        <waterfall-slot
          v-for="(item, index) in itemslist"
          :width="item.width"
          :height="item.height*2"
          :order="index"
          :key="item.index"
          move-class="item-move"
        >
          <div class="item" :style="item.style" :index="item.index">
              <img v-bind:src="item.url"  alt="">

              <template v-for="temp1 in content">

                      <div class="txt">
                        <h2>{{temp1.name}}</h2>
                        <p>{{temp1.desc}}</p>
                      </div>

                      <div class="user">
                        {{temp1.user.nickname}}
                      </div>
                </template>


          </div>

        </waterfall-slot>
      </waterfall>
        </div>
</div>
    </div>
</template>

<script>
// import Waterfall from '../libs/waterfall.vue';
// import WaterfallSlot from '../libs/waterfall-slot.vue';
    export default {
            // el(){
            //   return '#app'
            // },
            components: {
                'waterfall': Waterfall.waterfall,
                'waterfall-slot': Waterfall.waterfallSlot
            },
            data() {
                return {
                    align: 'center',
                    items: [
                       { "index": 0, "style": { "background": "rgba(195,123,177,.5)" }, "width": 116, "height": 129 },
                       { "index": 1, "style": { "background": "rgba(195,123,177,.5)" }, "width": 143, "height": 121 },
                       { "index": 2, "style": { "background": "rgba(255,230,135,.5)" }, "width": 114, "height": 127 },
                       { "index": 3, "style": { "background": "rgba(125,205,244,.5)" }, "width": 135, "height": 146 },
                       { "index": 4, "style": { "background": "rgba(255,230,135,.5)" }, "width": 116, "height": 131 },
                       { "index": 5, "style": { "background": "rgba(125,205,244,.5)" }, "width": 106, "height": 141 },
                       { "index": 6, "style": { "background": "rgba(255,230,135,.5)" }, "width": 117, "height": 138 },
                       { "index": 7, "style": { "background": "rgba(255,230,135,.5)" }, "width": 120, "height": 112 },
                      { "index": 8, "style": { "background": "rgba(125,205,244,.5)" }, "width": 132, "height": 100 },
                      { "index": 9, "style": { "background": "rgba(125,205,244,.5)" }, "width": 111, "height": 118 },
                      { "index": 10, "style": { "background": "rgba(194,217,78,.5)" }, "width": 144, "height": 133 }
                    ],
                    itemslist:[],
                    content:{},
                    isBusy: false
                }
            },

            methods: {

                    addItems: function () {
                    if (!this.isBusy && this.items.length < 500) {
                      this.isBusy = true
                      this.items.push.apply(this.items, ItemFactory.get(50))
                    }
                  },
                  shuffle: function () {
                    this.items.sort(function () {
                      return Math.random() - 0.5
                    })
                  },
                  reflowed: function () {
                    this.isBusy = false
                  }
            },
            mounted: function () {
                    this.$nextTick(function () {

                      this.$http.get('http://www.htmlk.cn/vuejson/index.json')
                          .then(
                              (res) => {
                                  this.list = res.body.data
                                        console.log(res.body.data);
                                        this.content=res.body.data;
                                        for(var i=0;i<res.body.data.length;i++){
                                         //console.log(res.body.data[i]);

                                                  this.itemslist.push(res.body.data[i].images_list[0])
                                            }
                                      //  console.log(this.itemslist);
                                        setTimeout(function(){
                                          var myScroll=new IScroll('#app',{
                                            scrollY:true
                                          });
                                        },1500)
                                      }
                                    )


                    })
                  }


    }
</script>



